.App {
  text-align: center;
  background-color: white;
}


/*
CONTENT PAGE
*/
.content {
  padding: 10vw 30vw;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  text-align: left;
  font-size: 1.5vw;
}

div .contentText{
  font-size: 4vw;
  line-height: 2vw;
  font-family:Verdana, Geneva, Tahoma, sans-serif
}
div .contentPara{
  font-size: 1.5vw;
  position: relative; 
  bottom: 3vw;
  font-family:serif
}
.header{
  padding: 1vw;
  text-align: right;
  background: white;
  color: black;
  font-size: 1vw;
  font-family:'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;

}

.image{
  position: absolute;
  width: 7%;
  top: 0.05vw;
  left: 0.1vw;
}

.corner{
  padding: 1vw 1vw;
}


.githubIcon{
  position:relative;
  width: auto; 
  top: 3vw;
  right: 20vw;
}